<template>
  <b-row>
    <b-col sm="6">
      <b-card title="Upload New Document" class="iq-mb-3">
        <b-form-select
          plain
          v-model="selected"
          :options="options1"
          id="exampleFormControlSelect1"
        >
          <template v-slot:first>
            <b-form-select-option :value="null" disabled
              >Select</b-form-select-option
            >
          </template>
        </b-form-select>
        <div v-if="selected == 'ID/Passport'">
          <b-form>
              <b-form-group label="Date of expiry" label-for="exampleInputdate">
                <b-form-input id="exampleInputdate" type="date" value="2020-12-18"></b-form-input>
            </b-form-group>
            <b-form-group label="Document Number " label-for="exampleInputText1">
                <b-form-input id="exampleInputText1" type="text"  ></b-form-input>
            </b-form-group>
            <b-form-group label="Type">
                <b-form-select plain v-model="selectedid" :options="options3" size="sm" class="mb-1">
                    <template v-slot:first>
                    <b-form-select-option :value="null">Open this select menu</b-form-select-option>
                    </template>
                </b-form-select>
                </b-form-group>
                <b-form-group label="Country of issue">
                <b-form-select plain v-model="selectedid" :options="options4" size="sm" class="mb-1">
                    <template v-slot:first>
                    <b-form-select-option :value="null">Open this select menu</b-form-select-option>
                    </template>
                </b-form-select>
                </b-form-group>
                    <b-form-group
              sm="12"
              class="mt-3"
              label-for="uploadFile"
              label="Front Side"
            >
              <b-form-file
                placeholder="Click/Drag Files To Upload"
                id="uploadFile"
              ></b-form-file>
            </b-form-group>
            <b-form-group
              sm="12"
              class="mt-3"
              label-for="uploadFile"
              label="Back Side"
            >
              <b-form-file
                placeholder="Click/Drag Files To Upload"
                id="uploadFile"
              ></b-form-file>
            </b-form-group>
             </b-form>
        </div>
        <div v-if="selected=='Prof of Adress'">
            <b-form>
            <b-form-group label="Country" class="mt-3">
                <b-form-select plain v-model="selectcountryadress" :options="options4" size="sm" class="mb-1">
                    <template v-slot:first>
                    <b-form-select-option :value="null">Open this select menu</b-form-select-option>
                    </template>
                </b-form-select>
                </b-form-group>
                <b-form-group label="City " label-for="exampleInputText1">
                <b-form-input id="exampleInputText1" type="text"  ></b-form-input>
            </b-form-group>
            <b-form-group label="Adress " label-for="exampleInputText1">
                <b-form-input id="exampleInputText1" type="text"  ></b-form-input>
            </b-form-group>
            <b-form-group label="Postal Code " label-for="exampleInputText1">
                <b-form-input id="exampleInputText1" type="text"  ></b-form-input>
            </b-form-group>
                 <b-form-group
              sm="12"
              class="mt-3"
              label-for="uploadFile"
              label="File"
            >
              <b-form-file
                placeholder="Click/Drag Files To Upload"
                id="uploadFile"
              ></b-form-file>
            </b-form-group>
            </b-form>
        </div>
         <b-button href="#" block variant="primary" class="mt-3"
          >Upload</b-button
        >
      </b-card>
    </b-col>
    <b-col sm="6">
      <b-card title="Uploaded Document" class="iq-mb-3">
        </b-card>
    </b-col>
  </b-row>
</template>
<script>
export default {
  data () {
    return {
      selected: '',
      selectedid: '',
      selectedcountry: '',
      selectcountryadress: '',
      options1: [
        { value: 'ID/Passport', text: 'ID/Passport' },
        { value: 'Prof of Adress', text: 'Prof of Adress' }
      ],
      options3: [
        { value: 'Passport', text: 'Passport' },
        { value: 'national id', text: 'national id' }
      ],
      options4: [
        { value: 'turkey', text: 'turkey' },
        { value: 'england', text: 'england' }
      ]

    }
  }
}
</script>
